@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lato&display=swap);
body{margin:0;font-family:"Karla", sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

button{display:inline-block;border:none;background:none;margin:0;text-decoration:none;font-size:1rem;cursor:pointer;text-align:center;transition:background 250ms ease-in-out, transform 150ms ease;-webkit-appearance:none;-moz-appearance:none}ul{list-style:none}*:focus{outline:1px #737373 dashed;outline-offset:2px}.members-list{display:flex;flex-wrap:wrap}.members-list__wrapper{max-width:1000px;margin:0 auto}.members-list__header{display:flex;align-items:center;justify-content:space-between;padding:0 15px}.members-list__tabs{padding:0 15px;display:flex;align-items:center}.members-list__tab{font-size:20px;border-radius:20px;padding:5px 10px}.members-list__tab:first-of-type{margin-right:20px}.members-list__tab--active{background:#454545;color:#ffffff}.members-list__sort-dropdown{margin-left:50px}.members-list__sort-dropdown select{font-size:15px;border-radius:20px;padding:5px 15px}

.member-card{display:flex;position:relative;flex-direction:column;margin:10px;padding:15px 10px;width:160px;height:235px;background:#fff;border:3px solid;border-radius:2px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);cursor:pointer}.member-card--D{border-color:#3775f0}.member-card--R{border-color:#e33434}.member-card__image-wrapper{width:140px;height:140px;border-radius:50%;overflow:hidden;margin:0 auto;position:relative;background-image:url("https://image.flaticon.com/icons/svg/760/760039.svg")}.member-card__image{width:100%;height:auto;color:rgba(0,0,0,0)}.member-name{display:flex;flex-direction:column;margin-top:10px;flex:1 1}.member-name__first,.member-name__last{margin:0}.member-name__first{font-style:italic;margin-left:5px;font-family:"Lato", sans-serif;font-size:0.75rem;text-transform:uppercase;color:#737373}.member-name__last{font-family:"Bebas Neue", cursive;font-size:1.7rem;white-space:nowrap;font-style:italic}.member-name__subtitle{margin:0;font-size:0.75rem;color:#737373;margin:auto 0}.wrapper{position:relative}

button{display:inline-block;border:none;background:none;margin:0;text-decoration:none;font-size:1rem;cursor:pointer;text-align:center;transition:background 250ms ease-in-out, transform 150ms ease;-webkit-appearance:none;-moz-appearance:none}ul{list-style:none}*:focus{outline:1px #737373 dashed;outline-offset:2px}.modal{position:fixed;background:#ffffff;min-height:450px;width:500px;border-radius:10px;top:50%;left:50%;transform:translate(-50%, -50%);padding:15px}.modal__wrapper{position:fixed;width:100vw;height:100vh;background:rgba(147,153,163,0.9);z-index:3;top:0;left:0}.modal__header{display:flex;height:35px;justify-content:space-between}.modal__title{margin:0;line-height:35px}.modal__close-button{margin:5px}.hidden{display:none}.img__wrapper{background:white;padding:5px;height:25px;width:25px;border-radius:50px}.img__wrapper img{width:100%;height:auto}.details{font-size:0.85rem;border-collapse:collapse;width:100%}.details td{padding:5px 10px;border:1px solid #dddddd}.details__committees-list{list-style:circle;padding-left:15px}.details__label{font-weight:900}.details__chart--party{display:flex}.details__icon{height:40px;margin:5px}.d3-container{height:100px;width:auto;padding:10px}#donutChart{height:100%;width:auto;transform:rotate(180deg)}

